.footer-logo {
    height: 4rem;
    filter: invert(1);
    padding-right: 0.5rem;
    padding-top: 0.75rem;
    transition: all .2s;

    &--fb {
        padding-left: 0.5rem;
    }


    &:hover {
        transform: scale(1.5) translateY(-1rem);
    }

}

.logo-text {
        font-size: 1rem;
}

